import React, {Component} from 'react';
import styled, {keyframes} from 'styled-components';

const userImage = 'http://drive.google.com/uc?export=view&id=1XMLkEY2H9gtwt455L1ncUY2-ie3uX6pl';

export default class Profile extends Component {
  state = {};

  render() {
    const {index} = this.props;
    return (
      <Container>
        {
          index === 1 ?
            <ContentWrapper>
              <ActiveTitle style={{paddingLeft: '2rem'}}>Who Am I</ActiveTitle>
              <ProfileSection>
                <RestrictBox>
                  <ActiveProfileImage/>
                </RestrictBox>
                <ActiveProfileTitle style={{textAlign: 'center'}}>
                  "가치"를 찾는 개발자
                  <ProfileContent>
                    단순 코딩 및 개발에 만족하지 않고,<br/>
                    서비스에 대한 비즈니스적 시각을 기르는 것과,<br/>
                    시장 반응에 따른 기능 강화를 추구합니다.
                  </ProfileContent>
                </ActiveProfileTitle>

              </ProfileSection>
            </ContentWrapper>
            :
            <ContentWrapper>
              <Title style={{paddingLeft: '2rem'}}>Who Am I</Title>
              <ProfileSection>
                <RestrictBox>
                  <ProfileImage/>
                </RestrictBox>
                <ProfileTitle style={{textAlign: 'center'}}>
                  "가치"를 찾는 개발자
                  <ProfileContent>
                    단순 코딩 및 개발에 만족하지 않고,<br/>
                    서비스에 대한 비즈니스적 시각을 기르는 것과,<br/>
                    시장 반응에 따른 기능 강화를 추구합니다.
                  </ProfileContent>
                </ProfileTitle>
              </ProfileSection>
            </ContentWrapper>
        }
      </Container>
    )
  }
}

const color = '#ff8b62';

const TextBox = styled.div`
  
`;

const scale = keyframes`
  0% {
    opacity: 0;
    transform: scale(0);
  }
  
  50% {
    opacity: 0;
    transform: scale(0.5);
  }
  
  100% {
    opacity: 1;
    transform: scale(1);
  }
`;

const fadeUp = keyframes`
  0% {
    opacity: 0;
    top: 50%;
  }
  
  100% {
    opacity: 1;
    top: 0%;
  }
`;

const RestrictBox = styled.div`
  width: 20rem;
  height: 20rem;
  flex: 1;
  position: relative;
`;

const ProfileSection = styled.div`
  flex: 8;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;
const ProfileImage = styled.div`
  width: 100%;
  height: 100%;
  background: url(${userImage}) no-repeat;
  background-size: contain;
  background-position: center center;
  position: absolute;
`;

const ActiveProfileImage = styled(ProfileImage)`
  animation: ${fadeUp} 2s ease;
`;

const fade = keyframes`
  0% {
    opacity: 0;
  }
  
  100% {
    opacity: 1;
  }
`;

const Container = styled.div`
  display: flex;
  flex: 1;
  width: 100%;
  height: 100%;
  background: #E73C7E;  
`;

const ContentWrapper = styled.div`
  display: inherit;
  flex-direction: column;
  flex: 1;
`;

const Title = styled.p`
  flex: 1;
  font-size: 5rem;
  color: #ffffff;
  font-weight: 700;
  padding: 2rem 0 0 0;
`;

const ProfileTitle = styled(Title)`
  
`;

const ActiveProfileTitle = styled(ProfileTitle)`
  animation: ${scale} 2s ease;
`;

const ActiveTitle = styled(Title)`
  animation: ${fade} 2s ease;
`;

const ProfileContent = styled.p`
  padding-top: 10%;
  font-size: 2.5rem;
  color: #ffffff;
  font-weight: 700;
`;