export default function Projects({index}) {
  return (
    <div style={{width: '100%', height: '100%', overflow: 'hidden'}}>
      {
        index === 3 ?
          <ActiveContainer>
            <ActiveTitle>Projects</ActiveTitle>
            <ContentWrapper>
              {
                windowWidth < 400 ?
                  <MobileProjectList/>
                  :
                  <ProjectList/>
              }
            </ContentWrapper>
          </ActiveContainer>
          :
          <Container>
            <Title>Projects</Title>

          </Container>
      }
    </div>
  )
};

