const colorpicker = '#494949';

const changeBackground = keyframes`
  0% {
    background: rgba(0,0,0,0.7);
  }
  
  100% {
    background: #08ffbd;
  }
`;

const jump = keyframes`
  0% {
    transform: translate(0,0);
  }
  
  30% {
    transform: translate(0,0);
  }
  
  70% {
    transform: translate(0,-0.3rem);
  }
  
  100% {
    transform: translate(0,0);
  }
`;

const changeTextColor = keyframes`
  0% {
    color: #ffffff;
  }
  
  100% {
    color: #e73c7e;
  }
`;

