import React, {Component} from 'react';
import styled from 'styled-components';
import ReactFullpage from '@fullpage/react-fullpage';
import Career from '../components/Career';
import Intro from '../components/Intro';
import Profile from '../components/Profile';
import Projects from '../components/Projects';
import Thankyou from '../components/Thankyou';

const anchors = ['Intro', 'Profile', 'Career', 'Projects', 'Thankyou'];

const fullpageOptions = {
  anchors: ['Intro', 'Profile', 'Career', 'Projects', 'Thankyou'],
  callbacks: ["onLeave", "afterLoad"],
  scrollOverflow: true
};

export default class HomeContainer extends Component {
  state = {
    nextIndex: 0,
  };

  componentDidMount() {
  }

  render() {
    const {nextIndex} = this.state;
    return (
      <ReactFullpage
        anchors={anchors}
        scrollOverflow={true}
        onLeave={(current, next, direction) => {
          this.setState(() => ({
            nextIndex: next.index,
          }))
        }}
        render={({state, fullpageApi}) => {
          console.log(state);
          return (
            <ReactFullpage.Wrapper
              {...fullpageOptions}
            >
              <div className="section">
                <Intro/>
              </div>
              <div className="section">
                <Profile index={nextIndex}/>
              </div>
              <div className="section">
                <Career index={nextIndex}/>
              </div>
              <div className="section">
                <Projects index={nextIndex}/>
              </div>
              <div className="section">
                <Thankyou index={nextIndex}/>
              </div>
            </ReactFullpage.Wrapper>
          );
        }}
      />
    )
  }
}


const RowContainer = styled.div`
  display: flex;
`;